.weather {
    box-sizing: border-box;
    position: relative;
    display: inline-block;
    float: left;
    width: 30%;
    min-width: 300px;
    font-size: 1.4em;
    padding: 2px 10px;
    margin: 2px 4px;
    border: 1px solid #ccc;
}
.weather.detailed {
    width: 100%;
    border: none;
}
.weather-header {
    position: relative;
    display: block;
    box-sizing: border-box;
}    
.weather-content {
    position: relative;
    display: block;
    box-sizing: border-box;
}    
.weather-data {
    box-sizing: border-box;
    position: relative;
    display: block;
    padding: 4px;
    margin: 0 auto;
    border: none;
}
.weather-icon {
    display: block;
    position: relative;
    max-width: 90%;
    margin: 0 auto;
}
.weather-details {
    position: relative;
    display: inline-block;
}
.weather.detailed .weather-data {
    display: inline-block;
    width: 30%;
    min-width: 300px;
    float: left;
    padding: 2px 10px;
    margin: 2px 4px;
    border: 1px solid #ccc;
}
.weather:after, .weather-content:after {
    content: "";
    display: table;
    clear: both;
}

.wind-dir {
    position: relative;
    display: inline-block;
    font-size: 1.4em;
}
.wind-dir:after {
    position: relative;
    display: inline-block;
    content: "âž™";
}

.wind-dir.e:after {
    transform: rotate(0deg);
}
.wind-dir.w:after {
    transform: rotate(-180deg);
}
.wind-dir.n:after {
    transform: rotate(-90deg);
}
.wind-dir.s:after {
    transform: rotate(90deg);
}
.wind-dir.ne:after {
    transform: rotate(-45deg);
}
.wind-dir.nw:after {
    transform: rotate(-135deg);
}
.wind-dir.se:after {
    transform: rotate(45deg);
}
.wind-dir.sw:after {
    transform: rotate(135deg);
}
.wind-dir.ene:after {
    transform: rotate(-25deg);
}
.wind-dir.wnw:after {
    transform: rotate(-155deg);
}
.wind-dir.ese:after {
    transform: rotate(25deg);
}
.wind-dir.wsw:after {
    transform: rotate(-205deg);
}
.wind-dir.nne:after {
    transform: rotate(-75deg);
}
.wind-dir.nnw:after {
    transform: rotate(-115deg);
}
.wind-dir.sse:after {
    transform: rotate(75deg);
}
.wind-dir.ssw:after {
    transform: rotate(115deg);
}
