body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.lds-facebook {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
  margin: 0 auto;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 6px;
  width: 13px;
  background: #d00;
  -webkit-animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
          animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 6px;
  -webkit-animation-delay: -0.24s;
          animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 26px;
  -webkit-animation-delay: -0.12s;
          animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 45px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes lds-facebook {
  0% {
    top: 6px;
    height: 51px;
  }
  50%, 100% {
    top: 19px;
    height: 26px;
  }
}
@keyframes lds-facebook {
  0% {
    top: 6px;
    height: 51px;
  }
  50%, 100% {
    top: 19px;
    height: 26px;
  }
}

.weather {
    box-sizing: border-box;
    position: relative;
    display: inline-block;
    float: left;
    width: 30%;
    min-width: 300px;
    font-size: 1.4em;
    padding: 2px 10px;
    margin: 2px 4px;
    border: 1px solid #ccc;
}
.weather.detailed {
    width: 100%;
    border: none;
}
.weather-header {
    position: relative;
    display: block;
    box-sizing: border-box;
}    
.weather-content {
    position: relative;
    display: block;
    box-sizing: border-box;
}    
.weather-data {
    box-sizing: border-box;
    position: relative;
    display: block;
    padding: 4px;
    margin: 0 auto;
    border: none;
}
.weather-icon {
    display: block;
    position: relative;
    max-width: 90%;
    margin: 0 auto;
}
.weather-details {
    position: relative;
    display: inline-block;
}
.weather.detailed .weather-data {
    display: inline-block;
    width: 30%;
    min-width: 300px;
    float: left;
    padding: 2px 10px;
    margin: 2px 4px;
    border: 1px solid #ccc;
}
.weather:after, .weather-content:after {
    content: "";
    display: table;
    clear: both;
}

.wind-dir {
    position: relative;
    display: inline-block;
    font-size: 1.4em;
}
.wind-dir:after {
    position: relative;
    display: inline-block;
    content: "\2799";
}

.wind-dir.e:after {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.wind-dir.w:after {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
.wind-dir.n:after {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}
.wind-dir.s:after {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
.wind-dir.ne:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.wind-dir.nw:after {
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}
.wind-dir.se:after {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.wind-dir.sw:after {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.wind-dir.ene:after {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
.wind-dir.wnw:after {
    -webkit-transform: rotate(-155deg);
            transform: rotate(-155deg);
}
.wind-dir.ese:after {
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
}
.wind-dir.wsw:after {
    -webkit-transform: rotate(-205deg);
            transform: rotate(-205deg);
}
.wind-dir.nne:after {
    -webkit-transform: rotate(-75deg);
            transform: rotate(-75deg);
}
.wind-dir.nnw:after {
    -webkit-transform: rotate(-115deg);
            transform: rotate(-115deg);
}
.wind-dir.sse:after {
    -webkit-transform: rotate(75deg);
            transform: rotate(75deg);
}
.wind-dir.ssw:after {
    -webkit-transform: rotate(115deg);
            transform: rotate(115deg);
}

.page {
    position: relative;
    display: block;
    box-sizing: border-box;
    padding: 20px 10px;
    width: 100%;
    max-width: 1640px;
    margin: 0 auto;
}
.search-box {
    position: relative;
    display: block;
}
.search-box input, .search-box button {
    position: relative;
    display: inline-block;
    padding: 8px 16px;
    margin: 8px;
    font-size: 1.2em;
    border-radius: 6px;
}
.search-box button {
    background: #c00;
    color: #efefef;
}

