.page {
    position: relative;
    display: block;
    box-sizing: border-box;
    padding: 20px 10px;
    width: 100%;
    max-width: 1640px;
    margin: 0 auto;
}
.search-box {
    position: relative;
    display: block;
}
.search-box input, .search-box button {
    position: relative;
    display: inline-block;
    padding: 8px 16px;
    margin: 8px;
    font-size: 1.2em;
    border-radius: 6px;
}
.search-box button {
    background: #c00;
    color: #efefef;
}
